#images ul {
    list-style: none;
    margin: 0px;
    padding:0px;
}

#images ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
#images li{
    width: 15%;
    margin: 5px;
}

#images ul img{
    border-radius: 50%;
    border: 2px solid #fff;
    height: 200px;
    width: 100%;
    transition: 1s;
}
img:hover
{
    transform: scale(0.9) translateY(0px);
}

@media screen and (max-width : 768px){
    #images li {
        width: 20%;
        margin: 2px;
    }
    #images ul img{
        height: 80px;
    }
}